$(document).ready(function() {

const subItem = document.querySelectorAll('.sub-menu .menu-item');
const subMenu = document.querySelectorAll('.sub-menu');

subItem.forEach(item => {
    if (item.classList.contains('menu-item__label')) {
        item.parentElement.classList.add("JS-sub-menu--label");
    }
});

function setStickyMobileMenu() {
    if (window.pageYOffset >= document.querySelector('.o-header').offsetHeight) {
        document.querySelector('.o-header').classList.add("sticky");
    }  else {
        document.querySelector('.o-header').classList.remove("sticky");
    }
}

if (document.getElementById("header-main") != null) {
    let windowWidth = window.innerWidth;

    window.addEventListener("resize", () => {
        windowWidth = window.innerWidth;

        if (windowWidth < 1199.98) {
            window.onscroll = function() {
                setStickyMobileMenu();
            };
        }
    });

    if (windowWidth < 1199.98) {
        window.onscroll = function() {
            setStickyMobileMenu();
        };
    }


    window.onload = function() {
        setStickyMobileMenu();
    };

}

if (document.querySelector(".wpml-ls-item-legacy-dropdown") != null) {
    let languageList = document.querySelector('.wpml-ls-item-legacy-dropdown');
    let languageListLink = document.querySelector('.js-wpml-ls-item-toggle');
    let windowWidth = window.innerWidth;

    let toggleActiveLanguageClass = function(windowWidth) {
        if (windowWidth < 1199.98) {
            languageListLink.addEventListener('click', (e)=> {
                e.preventDefault();
                languageList.classList.toggle('is-open');
            });
            $(document).on('click', function (e) {
                if (!$(e.target).closest('.wpml-ls-statics-shortcode_actions').length) {
                    $(languageList).removeClass('is-open');
                }
                e.stopPropagation();
            });
        } else {
            languageList.classList.remove('is-open');
        }
    };

    toggleActiveLanguageClass(windowWidth);

    window.addEventListener("resize", () => {
        windowWidth = window.innerWidth;

        toggleActiveLanguageClass(windowWidth);
    });

}
});

(function ($) {
    $(document).ready(function() {
        let linkOpen = $('.JS-link-menu--open');
        let linkClose = $('.JS-link-menu--close');
        let itemHasChildren = $('.menu-item-has-children');
        let mainMenu = $('.c-main-menu-mobile');
        let header = $('.o-header');

        linkOpen.click(function() {
            mainMenu.addClass('is-open');
            $('body').css('overflow', 'hidden');
            header.removeClass('sticky');
        });

        linkClose.click(function() {
            mainMenu.removeClass('is-open');
            $('body').css('overflow', 'initial');
        });

        itemHasChildren.click(function() {
            $(this).toggleClass('is-active');
        });
    });
})(jQuery);