$(document).ready(function () {

    $(".JS--jobs-filter-btn").click(function () {
        var location = $(this).data('job-location');
        var department = $(this).data('job-department');
        var token = $('.s-job-board__filters').data('token-url');

        $.ajax({
            url: appLocations.admin_ajax,
            type: 'POST',
            data: {
                'action': "jobs_filter",
                'location': location,
                'department': department,
                'token': token,
            },
            success: function (result) {
                $('.JS--ajax-job-container').html(result).fadeIn(250);
            },

            error: function (errorThrown) {
                console.log(errorThrown);
            }
        });

    });

});